	state = {
		currencyExchange: {},
		cartItems: this.props.cartData,
		taxPercent: 2,
		shippingCharge: 0,
		totalAmount: 0,
	};

	componentDidMount() {
		this.getCurrencyExchange();
	}

	getCurrencyExchange() {
		CurrencyService.getCurrencyDetails()
			.then((res) => {
				console.log("success", res);
				this.setState({ currencyExchange: res.data.Exchange }); // update list with data from server
			})
			.catch((err) => {
				console.log("error", err);
			});
	}

	onCheckout = (e) => {
		//Get all checked out product ids
		let checkoutProdIds = [];
		if (this.props.checkoutData != null && this.props.checkoutData.length > 0) {
			checkoutProdIds = this.props.checkoutData.map((prod) => {
				return prod.productId;
			})
		}
		this.state.cartItems.map((val) => {

			//If cart product is not checked out (not available in checkout list)
			if (!checkoutProdIds.includes(val.productId)) {

				//Add only 1 unit by default
				//val.productQty = 1;

				// add to checkout store
				return this.props.addItem(val);
			}
		});
		this.props.history.push("/checkout"); // navigate to checkout
	}

	onQuantityChange = (e, productId) => {
		if (!isNaN(Number(e.target.value))) {
			this.setState({
				cartItems: this.state.cartItems.map(item => {
					if (item.productId == productId) {
						item.productQty = Number(e.target.value);
					}
					return item;
				}),
			});
		}
	}

	calculateTotalAmount = (prodId) => {
		return (this.state.cartItems.reduce((sum, item) => {

			if (!isNaN(prodId) && Number(prodId) > 0) {
				if (prodId == item.productId) {
					sum = (
						Number(sum) + Number(item.productPrice) * Number(item.productQty)
					);
				}
			}
			else {
				sum = (
					Number(sum) + Number(item.productPrice) * Number(item.productQty)
				);
			}

			return sum;
		}, 0) * Number(this.state.currencyExchange[this.props.selectedCode])).toFixed(2);
	}

	calculateNetPayableAmount = () => {
		debugger;
		return (
			Number(this.calculateTotalAmount()
				+ this.calculateTotalAmount() * (this.state.taxPercent > 0 ? this.state.taxPercent / 100 : 0)
				+ this.state.shippingCharge).toFixed(2)
		);
	}

	onQtyBtnClick = (e, productId) => {
		debugger;
		if (e.currentTarget.id === "btnIncreaseQty") {
			this.setState({
				cartItems: this.state.cartItems.map(item => {
					if (item.productId == productId) {
						item.productQty = Number(item.productQty) + 1;
					}
					return item;
				}),
			});
		}
		else {
			this.setState({
				cartItems: this.state.cartItems.map(item => {
					if (item.productId == productId && (Number(item.productQty) > 0)) {
						item.productQty = Number(item.productQty) - 1;
					}
					return item;
				}),
			});
		}
	}

