const mapStoreDataToProps = (storeData) => {
	return {
		selectedCode: storeData.currency,
		cartData: storeData.cart,
		checkoutData: storeData.checkout,
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		removeItem: (p) => dispatch(CartActions.removeItem(p)),
		addItem: (p) => dispatch(CheckoutActions.addItem(p)),
	};
};

export default connect(mapStoreDataToProps, mapDispatchToProps)(Cart);