import CheckoutActions from "../actions/checkout-actions";

function checkoutReducer(storeData = [], action) {
    switch (action.type) {
        case CheckoutActions.ActionTypes.ADD_ITEM:
            debugger;
            const result = storeData.find(item => item.productId === action.product.productId);
            return result == null? [...storeData, action.product]: storeData;
            //return ;
        case CheckoutActions.ActionTypes.REMOVE_ITEM:
            return storeData.filter((p) => p.productId !== action.id);
        case CheckoutActions.ActionTypes.UPDATE_ITEM:
            return storeData.map((p) => {
                debugger;
                if(p.productId == action.id.productId)
                {
                     p.productQty=action.id.productQty
                 } 
                 return p;
                })

            // storeData.cart.map(item => {
            //     item.productQty = 1;
            //     return item;
            //   }),
            //return [...storeData, action.product];
        default:
            return storeData;
    }
}

export default checkoutReducer;