import React from "react";

// state={variable:initialize,variable1:,variable2}
// state : initialized, setState
function ThemeSwitch(props) {
  // let [state_variable,setState]= React.useState(default value); // destructuring
  let [theme, setTheme] = React.useState("light");
  // lifecycle : mounting, updating, unmounting
  React.useEffect(() => {
    console.log("USE EFFECT CALLED");
    document.body.className = "bg-" + theme;
  });

  
  if (theme === "light") {
    return (
      <button
        className="btn btn-default"
        onClick={() => {
          setTheme("dark");
          props.changeTheme("dark");
        }}
      >
        <i class="fas fa-moon text-secondary fa-2x"></i>
      </button>
    );
  }
  return (
    <button
      className="btn btn-default"
      onClick={() => {
        setTheme("light");
        props.changeTheme("light");
      }}
    >
      <i class="far fa-sun text-warning fa-2x"></i>
    </button>
  );
}
export default ThemeSwitch;
