import React from "react";
import {Link, NavLink} from "react-router-dom";

function MainMenu(props) {
    return (
        <ul className="nav navbar-nav collapse navbar-collapse">
            <li><NavLink className="nav-link" to="/" exact><a className="active">Home</a></NavLink></li>
            <li className="dropdown"><a href="#">Shop<i className="fa fa-angle-down"></i></a>
                <ul role="menu" className="sub-menu">
                    <li><NavLink className="nav-link" to="/products" exact><a>Products</a></NavLink></li>
                    {/* <li><NavLink className="nav-link" to="/product-details" exact><a>Product Details</a></NavLink></li> */}
                    <li><NavLink className="nav-link" to="/checkout" exact><a>Checkout</a></NavLink></li>
                    <li><NavLink className="nav-link" to="/cart" exact><a>Cart</a></NavLink></li>
                    <li><NavLink className="nav-link" to="/login" exact><a>Login</a></NavLink></li>
                </ul>
            </li>
            <li className="dropdown"><NavLink className="nav-link" to="/" exact><a href="#">Blog<i className="fa fa-angle-down"></i></a></NavLink>
                <ul role="menu" className="sub-menu">
                    <li><NavLink className="nav-link" to="/blog" exact><a>Blog List</a></NavLink></li>
                    <li><NavLink className="nav-link" to="/blog-single" exact><a>Blog Single</a></NavLink></li>
                </ul>
            </li>
            {/* <li><NavLink className="nav-link" to="/contact" exact><a>Contact</a></NavLink></li> */}
            <li><Link className="nav-link" to="/contact" exact><a>Contact</a></Link></li>
            {/* <li><a href="/contact">Contact</a></li> */}
        </ul>
    );
}
export default MainMenu;