import React from "react";
import { useHistory } from "react-router-dom";
import "../../Stylesheets/Custom.css"

function CartIcon(props) {

  let history = useHistory();

  function handleClick() {
    history.push("/cart");
  }

  if (props.theme === "light") {
    return (
      <button
      className="btn btn-default"
      onClick={handleClick}
    >
      <i class="fab fa-opencart" data-toggle="tooltip" data-placement="top" title="My Cart"></i>
    </button>
    );
  }
  return (
    <button
      className="btn btn-default"
      onClick={handleClick}
    >
      <i class="fab fa-opencart fa_custom" data-toggle="tooltip" data-placement="top" title="My Cart"></i>
    </button>
  );
}
export default CartIcon;
