import React from "react";
import { Route, Switch } from "react-router-dom";
import Demo from "./Demo";
// import ProductList from "./containers/ProductList";
import ErrorPage from "./components/content-panel-components/ErrorPage";
import Login from "./containers/Login";
import Cart from "./containers/Cart";
import Checkout from "./containers/Checkout";
import PrivateRoute from "./components/content-panel-components/PrivateRoute";
import Home from "./containers/home";
import ContactUs from "./containers/ContactUs";
import Blog from "./containers/Blog";
import BlogSingle from "./containers/BlogSingle";
import ProductDetails from "./containers/ProductDetails";
import Account from "./containers/Account";

const LazyProductList = React.lazy(() => import("./containers/ProductList"));

function AppRouter(props) {
  return (
    <div className="container-fluid">
      <React.Suspense fallback={<div>Loading...</div>}>
        <Switch>
          <Route path="/" component={Home} exact={true} />
          <Route path="/products" component={LazyProductList} />
          <Route path="/login" component={Login} />
          <Route path="/cart" component={Cart} />
          <Route path="/contact" component={ContactUs} />
          {/* <PrivateRoute path="/checkout" component={Checkout} /> */}
          <Route path="/checkout" component={Checkout} />
          <Route path="/blog" component={Blog} />
          <Route path="/blog-single" component={BlogSingle} />
          <Route path="/account" component={Account} />
          {/* <Route path="/product-details/:productId" component={ProductDetails} /> */}
          <Route path="/product-details" component={ProductDetails} />
          
          <Route component={ErrorPage} />
        </Switch>
      </React.Suspense>
    </div>
  );
}
export default AppRouter;
