import React from "react";
import Currency from "./Currency";
import Column from "./Column";
import ImageWithFallback from "./ImageWithFallback";
import QuantitySelector from "./QuantitySelector";
import { NavLink } from "react-router-dom";

class CartProduct extends React.Component {
   
    render() {
        debugger;
        const { pData, wishlist, code, exchange, addedInCart, isRemove, isCheckout, quantityChange, qtyBtnClick, calculateTotalAmount, removeItemBtnClick, redirectPath } = this.props;
        return (
            // <Column size={4}>
            //   <div className="py-2 px-2 my-2 text-center shadow rounded">
            //     <i class="fas fa-heart wishlist shadow"></i>
            //     <ImageWithFallback source={pData.productImage} />
            //     <div className="mt-4">
            //       <h4>{pData.productName}</h4>
            //       <h5>
            //         {code} {Number(Number(pData.productPrice) * Number(exchange)).toFixed(2)}
            //       </h5>
            //       {/* <button>Add to {wishlist ? "Wishlist" : "Cart"}</button> */}
            //       {this._renderStock(pData.productStock, wishlist, addedInCart, isRemove, isCheckout)}
            //     </div>
            //   </div>
            // </Column>
            <tr>
                <td class="cart_product">
                    <a href=""><ImageWithFallback source={pData.productImage} /></a>
                </td>
                <td class="cart_description">
                    <h4><a href="">{pData.productName}</a></h4>
                    <p>Web ID: {pData.productId}</p>
                </td>
                <td class="cart_price">
                    <p>{code} {Number(Number(pData.productPrice) * Number(exchange)).toFixed(2)}</p>
                </td>
                <td class="cart_quantity">
                    <QuantitySelector
                    quantity={pData.productQty}
                    quantityChange={quantityChange}
                    qtyBtnClick={qtyBtnClick}
                    productId={pData.productId}
                    key={pData.productId}
                    redirectPath={redirectPath}
                    />
                </td>
                <td class="cart_total">
                    <p class="cart_total_price">{code} {calculateTotalAmount(pData.productId)}</p>
                </td>
                <td class="cart_delete">
                <NavLink className="nav-link" to="/cart" exact                
                    onClick={(e) => removeItemBtnClick(pData.productId)}  
                    productId={pData.productId} 
                    key={"btn2_" + pData.productId}
                    id="btnRemoveItemFromCart">
                        <a class="cart_quantity_delete"><i class="fa fa-times"></i></a>
                 </NavLink>
                </td>
            </tr>
        );
    }
}

export default CartProduct;
