import React from "react";
import Axios from "axios";
import Menu from "../components/content-panel-components/Menu";

class Header extends React.Component {
  state = { menuList: [] };

  async componentDidMount() {
    try {
      const res = await Axios.get("/menu.json");
      const menuList = res.data;
      this.setState({ menuList });
    } catch (e) {
      console.log("Error", e);
    }
  }

  render() {
    return (
      <nav
        className={`fixed-top shadow-sm navbar navbar-expand-lg navbar-${this.props.theme} bg-${this.props.theme}`}
      >
        <a className="navbar-brand" href="#">
          Navbar
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <Menu menuData={this.state.menuList} />
        {/* OVER HERE */}
        {this.props.children}
      </nav>
    );
  }
}
export default Header;
