import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import Helmet from "react-helmet";

function Account(props) {
    //Array destructuring
    const [viewProfile, setProfileView] = useState(true);

    return (
        <section>
            <div className="container">
                {viewProfile ?

                    (
                        <div className="shopper-informations">
                            <div className="row">
                                <div className="col-sm-4">
                                    <div className="shopper-info">
                                        <h3>Profile</h3>
                                        <p>Name: Abhijit Banik</p>
                                        <p>Email: abhijitbanik2006@gmail.com</p>
                                    </div>
                                </div>
                                <div className="col-sm-8 clearfix">
                                    <div className="bill-to">
                                        <h3>Address</h3>
                                        <div className="">
                                            <p>7th and 8th Floor, Campus 5B, Plot B, RMZ Ecoworld, SEZ Campus, Outer Ring Road, Bengaluru, Karnataka 560103</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="row text-center">
                                <div className="col-sm-8 btn">
                                    <NavLink className="nav-link" to="/account" onClick={() => setProfileView(false)} exact><a className="btn btn-primary" href="">Edit</a></NavLink>
                                </div>
                                <div className="col-sm-4">
                                </div>
                            </div>
                        </div>
                    ) :

                    (<div className="shopper-informations">
                        <div className="row">
                            <div className="col-sm-6">
                                <div className="shopper-info">
                                    <h3>Profile</h3>
                                    <form>
                                        <input type="text" placeholder="Display Name" />
                                        <input type="text" placeholder="First Name *" />
                                            <input type="text" placeholder="Middle Name" />
                                            <input type="text" placeholder="Last Name *" />
                                        <input type="text" placeholder="User Name" />
                                        <input type="password" placeholder="Password" />
                                        <input type="password" placeholder="Confirm password" />
                                    </form>
                                </div>
                            </div>
                            <div className="col-sm-6 clearfix">
                                <div className="bill-to">                                    
                                    <h3>Address</h3>
                                    <div className="form-one">
                                        <form>
                                            <input type="text" placeholder="Company Name" />
                                            <input type="text" placeholder="Email*" />
                                            <input type="text" placeholder="Title" />                                            
                                            <input type="text" placeholder="Address 1 *" />
                                            <input type="text" placeholder="Address 2" />
                                        </form>
                                    </div>
                                    <div className="form-two">
                                        <form>
                                            <input type="text" placeholder="Zip / Postal Code *" />
                                            <select>
                                                <option>-- Country --</option>
                                                <option>United States</option>
                                                <option>Bangladesh</option>
                                                <option>UK</option>
                                                <option>India</option>
                                                <option>Pakistan</option>
                                                <option>Ucrane</option>
                                                <option>Canada</option>
                                                <option>Dubai</option>
                                            </select>
                                            <select>
                                                <option>-- State / Province / Region --</option>
                                                <option>United States</option>
                                                <option>Bangladesh</option>
                                                <option>UK</option>
                                                <option>India</option>
                                                <option>Pakistan</option>
                                                <option>Ucrane</option>
                                                <option>Canada</option>
                                                <option>Dubai</option>
                                            </select>
                                            {/* <input type="password" placeholder="Confirm password" /> */}
                                            <input type="text" placeholder="Phone *" />
                                            <input type="text" placeholder="Mobile Phone" />
                                            <input type="text" placeholder="Fax" />
                                        </form>

                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row text-center">
                            <div className="btn btn-space">
                            <NavLink className="nav-link" to="/account" onClick={() => setProfileView(true)} exact><a className="btn btn-primary" href="">Save</a></NavLink>
                            </div>
                            <div className="btn">
                            <NavLink className="nav-link" to="/account" onClick={() => setProfileView(true)} exact><a className="btn btn-primary" href="">Cancel</a></NavLink>
                            </div>
                        </div>
                    </div>)
                }
            </div>
        </section>

    );
}
export default Account;