import React from 'react';
import { NavLink } from "react-router-dom";

function QuantitySelector(props) {
    return (
        // <div>
        //     <button id="btnDecreaseQty" 
        //         onClick={(e) => props.qtyBtnClick(e, props.productId)} 
        //         productId={props.productId} 
        //         key={"btn1_" + props.productId}
        //     >-</button>

        //     <input id={"txtQuantity_" + props.productId} 
        //         value={props.quantity} 
        //         onChange={(e) => props.quantityChange(e, props.productId)} 
        //         name="quantity" 
        //         productId={"txt_" + props.productId} key={props.productId} 
        //     />

        //     <button id="btnIncreaseQty" 
        //         onClick={(e) => props.qtyBtnClick(e, props.productId)} 
        //         productId={props.productId} 
        //         key={"btn2_" + props.productId}
        //     >+</button>
        // </div>


        <div class="cart_quantity_button">
            
            <NavLink className="nav-link" to={props.redirectPath} exact                
                onClick={(e) => props.qtyBtnClick(e, props.productId)}  
                 productId={props.productId} 
                 key={"btn2_" + props.productId}
                 id="btnDecreaseQty">
                     <a class="cart_quantity_down" > - </a>
            </NavLink>

            <input class="cart_quantity_input" type="text" autocomplete="off" size="2" 
                    id={"txtQuantity_" + props.productId} 
                    value={props.quantity} 
                    onChange={(e) => props.quantityChange(e, props.productId)} 
                    name="quantity" 
                    productId={"txt_" + props.productId} key={props.productId} 
            />
            
            <NavLink className="nav-link" to={props.redirectPath} exact
                id="btnIncreaseQty" 
                onClick={(e) => props.qtyBtnClick(e, props.productId)} 
                productId={props.productId} 
                key={"btn2_" + props.productId}>
                    <a class="cart_quantity_up" > + </a>
            </NavLink>
            
        </div>

    )
}

export default QuantitySelector;