import CartActions from "../actions/cart-actions";

function cartReducer(storeData = [], action) {
    switch (action.type) {
        case CartActions.ActionTypes.ADD_TO_CART:
          return [...storeData, action.product];
        case CartActions.ActionTypes.REMOVE_ITEM:
          return storeData.filter((p) => p.productId !== action.id);
        case CartActions.ActionTypes.UPDATE_ITEM:
          return storeData.map((p) => {
              debugger;
              if(p.productId == action.id.productId)
              {
                   p.productQty=action.id.productQty
               } 
               return p;
              })

        default:
          return storeData;
      }
}

export default cartReducer;