import React from "react";
import { connect } from "react-redux";
import CurrencyActions from "../../store/actions/currency-actions";
import CurrencyService from "../../services/currency-service";

class Currency extends React.Component {
  state = {
    currencyList: [],
  };

  componentDidMount() {
    this.getData();
  }

  getData() {
    CurrencyService.getCurrencyDetails()
      .then((res) => {
        console.log("success", res);
        this.setState({ currencyList: res.data.CurrencyList }); // update list with data from server
      })
      .catch((err) => {
        console.log("error", err);
      });
  }

  render() {
    const codes = this.state.currencyList; //["INR", "USD", "CAD", "EUR"];
    return (
      <select
        className={`form-control col-1 bg-${this.props.theme} text-${
          this.props.theme === "light" ? "dark" : "light"
        }`}
        onChange={(event) => this.props.currencyChange(event.target.value)}
      >
        {codes.map((c) => (
          <option key={c}>{c}</option>
        ))}
      </select>
    );
  }
}
// connect(what to retrieve, what to modify)(component)
const mapDispatchToProps = (dispatch) => {
  return {
    currencyChange: (code) => dispatch(CurrencyActions.updateCurrency(code)),
  };
};
export default connect(null, mapDispatchToProps)(Currency);
