import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import UserActions from "../../store/actions/user-actions";

function LoginButtons(props) {
  if (props.isLoggedIn) {
    return (
      <button
        type="button"
        className="btn btn-outline-info ml-2"
        onClick={() => props.logout()}
      >
        Logout
      </button>
    );
  }
  return (
    <Link to="/login" className="btn btn-outline-info  ml-2">
      Login
    </Link>
  );
}
// connect to store
const mapStoreDataToProps = (storeData) => {
  return {
    isLoggedIn: !!storeData.userSession.user, // null : false, object: true
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    logout: () => dispatch(UserActions.logout()),
  };
};
export default connect(mapStoreDataToProps, mapDispatchToProps)(LoginButtons);
