import React, { Component } from "react";
import emailjs from 'emailjs-com';

class EmailForm extends React.Component {  

//EmailJS details
//https://dashboard.emailjs.com/admin
//UserId: abhijitbanik20062gmail.com
//Ins@1234

    sendEmail = (e) => {
        e.preventDefault();

        emailjs.sendForm('gmail', 'template_3ugv7cu', e.target, 'user_lr0rAGYvUv6n2qILPZq3z')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });

        e.target.reset();
    }

    render() {        

        return (
            <form id="main-contact-form" className="contact-form row" name="contact-form" onSubmit={this.sendEmail}>
                <div className="form-group col-md-6">
                    <input type="text" name="name" className="form-control" required="required" placeholder="Name" />
                </div>
                <div className="form-group col-md-6">
                    <input type="email" name="to_email" className="form-control" required="required" placeholder="Email" />
                </div>
                <div className="form-group col-md-12">
                    <input type="text" name="subject" className="form-control" required="required" placeholder="Subject" />
                </div>
                <div className="form-group col-md-12">
                    <textarea name="message" id="message" required="required" className="form-control" rows="8" placeholder="Your Message Here"></textarea>
                </div>
                <div className="form-group col-md-12">
                    <input type="submit" name="submit" className="btn btn-primary pull-right" value="Submit" />
                </div>
            </form>

        );
    }
}

export default EmailForm;