import React from "react";
import { NavLink } from "react-router-dom";

function ShopMenu(props) {

    return (
        <ul className="nav navbar-nav">
            <li><NavLink
                className="nav-link"
                to="/account"
                exact
            ><a><i className="fa fa-user"></i> Account</a></NavLink></li>
            <li><NavLink
                className="nav-link"
                to="/cart"
                exact
            ><a><i className="fa fa-star"></i> Wishlist</a></NavLink></li>
            <li><NavLink
                className="nav-link"
                to="/checkout"
                exact
            ><a><i className="fa fa-crosshairs"></i> Checkout</a></NavLink></li>

            <li><NavLink
                className="nav-link"
                to="/cart"
                exact
            >
                <a><i className="fa fa-shopping-cart"></i> Cart</a>
            </NavLink></li>
            <li><NavLink
                className="nav-link"
                to="/login"
                exact
            ><a><i className="fa fa-lock"></i> Login</a></NavLink></li>
        </ul>
    );
}
export default ShopMenu;