import React from "react";
import Column from "./components/content-panel-components/Column";

class Demo extends React.Component {
  state = { no: 1, count: 0, testData: "" };

  shouldComponentUpdate(nextProps, nextState) {
    console.log("should component update", nextProps, nextState);
    return nextState.count === 5 || nextState.no !== this.state.no; //boolean
  }

  render() {
    console.log("RENDER CALLED", this.state);
    const name = "Mike";
    return (
      <div className="row">
        <Column size={1}>
          <h1>Demo Component</h1>
          <p>some sample content</p>
          {this.state.count === 5 ? <p>hello from {name}</p> : null}
          <p>{5 + 8}</p>
          <input
            type="text"
            onChange={(e) => this.setState({ testData: e.target.value })}
          />
          <button
            onClick={() =>
              this.props.history.push("/cart?test=" + this.state.testData)
            }
          >
            Navigate to Cart
          </button>
          <button
            onClick={() => this.setState({ no: 1 })}
            className="btn btn-light"
          >
            Change to 1
          </button>
          <button
            onClick={() => this.setState({ no: 0 })}
            className="btn btn-light mx-4"
          >
            Change to 0
          </button>
          <button
            className="btn btn-light"
            onClick={() =>
              this.setState((prevState) => ({ count: prevState.count + 1 }))
            }
          >
            Increment Count
          </button>
        </Column>
      </div>
    );
    // return null;
  }
}
export default Demo;
