const ActionTypes = {
    ADD_ITEM: "[Checkout] Add item",
    REMOVE_ITEM: "[Checkout] Remove item",
    UPDATE_ITEM: "[Checkout] Update item",
  };
  const addItem = (product) => {
    return {
      type: ActionTypes.ADD_ITEM,
      product,
    };
  };
  const removeItem = (id) => {
    return {
      type: ActionTypes.REMOVE_ITEM,
      id,
    };
  };
  const updateItem = (product) => {
    return {
      type: ActionTypes.UPDATE_ITEM,
      product,
    };
  };
  const CheckoutActions={ActionTypes, addItem, removeItem, updateItem};
  
  export default CheckoutActions;