import React from "react";
import { Redirect, Route } from "react-router-dom";
import { connect } from "react-redux";

function PrivateRoute({ component: RoutedComponent, isLoggedIn, ...rest }) {
  return (
    <Route
      {...rest}
      render={({ location }) =>
        isLoggedIn ? (
          <RoutedComponent />
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location.pathname },
            }}
          />
        )
      }
    />
  );
}
const mapStoreDataToProps = (storeData) => {
  return {
    isLoggedIn: !!storeData.userSession.user, // null : false, object: true
  };
};
export default connect(mapStoreDataToProps)(PrivateRoute);
