import CurrencyActions from "../actions/currency-actions";
import CurrencyService from "../../services/currency-service";
import React from "react";

// reducer(storeData,action)
// state : initialised

function getDefaultCurrency() {
  debugger;
  let defaultCurr = null;
  
  CurrencyService.getCurrencyDetails()
    .then((res) => {
      console.log("success", res);
      defaultCurr = res.data.DefaultCurrency.Currency; // update list with data from server
    })
    .catch((err) => {
      console.log("error", err);
    });

    return defaultCurr;
}

function currencyReducer(storeData = "INR ₹", action) {  
  
  switch (action.type) {
    case CurrencyActions.actionTypes.UPDATE_CURRENCY:
      return action.code;
    default:
      return storeData; // current data
  }
}
export default currencyReducer;
